.root {
  width: 100%;
  cursor: move;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 30px;
  padding-bottom: 0;
}

.titleBox {
  display: flex;
  align-items: center;
}
.title {
  color: white;
  font-size: 1.375rem;
  margin: 0;
  display: inline;
}

.closeIcon {
  cursor: pointer;
  opacity: 0.5;
  transition: opacity 0.2s;

  width: 24px;
  height: 24px;

  & > path {
    fill: white;
  }

  &:hover {
    opacity: 1;
  }
}
